  font-family: "Callingstone";
  src: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  font-weight: 500;
  font-style: normal;
}

@font-face {
  font-family: "Sedan";
  src: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
  font-weight: 500;
  font-style: normal;
}

:root {
  --accent-color: #0c0100;
  --background-color: #fcf7ec;
}

body {
  margin: 0;
  box-sizing: border-box;
  min-height: 100vh;
  display: flex;
  background-size: cover;
  background-position: center;
  background-color: var(--background-color);
}

header {
  display: flex;
  flex-flow: column;
  justify-content: space-between;
  align-items: center;
  font-family: "Sedan", sans-serif;
  color: var(--background-color);
  height: 100vh;
  min-width: 20vw;
  text-align: center;
  z-index: 1;
  background-color: var(--accent-color);
}

header > h1 {
  font-size: 2em;
  padding-top: 5vh;
  font-family: "Callingstone", sans-serif;
}

nav {
  display: flex;
  width: 12vw;
  flex-flow: column;
  gap: 3vw;
  align-items: center;
}

nav > button {
  width: 5vw;
  height: 5vh;
  border: none;
  background: none;
  color: var(--background-color);
  font-size: 1.2em;
  text-align: center;
  font-family: "Sedan", sans-serif;
  border: solid 1px var(--accent-color);
}

nav > button:hover {
  border-bottom: solid 1px var(--background-color);
}

.headerBottom {
  padding-bottom: 5vh;
  font-size: 1.1em;
}

.headerBottom > img {
  height: 35px;
}

#content {
  display: grid;
  grid-template-rows: 1fr;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 1.2vw;
  margin: 1.2vw;
  font-family: "Sedan", sans-serif;
  min-width: 77.8vw;
}

#content > img {
  width: 25vw;
  height: 95vh;
  object-fit: cover;
}

#content > div {
  text-align: center;
  border: solid 2px var(--accent-color);
  padding: 20px;
  color: var(--accent-color);
  display: flex;
  flex-flow: column;
  justify-content: space-between;
  align-items: center;
}

#content h2 {
  font-size: 2em;
}

#content h3 {
  font-size: 1.3em;
}

#content p {
  font-size: 1em;
}

#content > div > div {
  text-align: center;
  border: solid 1px var(--accent-color);
  padding: 20px;
  color: var(--accent-color);
  list-style: none;
  width: 20vw;
}
