import image1 from '../Resources/food1.jpg'
import image2 from '../Resources/food2.jpg'
import image3 from '../Resources/food3.jpg'
const container = document.getElementById("content")

export function homeLoad() {
    while (container.firstChild) {
        container.removeChild(container.lastChild);
    };

    let pic1 = document.createElement("img");
    let pic2 = document.createElement("img");
    let pic3 = document.createElement("img");

    pic1.src = image1;
    pic2.src = image2;
    pic3.src = image3;

    container.appendChild(pic1);
    container.appendChild(pic2);
    container.appendChild(pic3);

}