import { homeLoad } from "./home"
import { aboutLoad } from "./about"
import { menuLoad } from "./menu"
import './style.css'
import instaIcon from "../Resources/instagram.svg"
import facebookIcon from "../Resources/facebook.svg"
import twitterIcon from "../Resources/twitter.svg"

const homeButton = document.getElementById("home")
const aboutButton = document.getElementById("about")
const menuButton = document.getElementById("menu")
const img1 = new Image();
const img2 = new Image();
const img3 = new Image();
const footer = document.querySelector(".headerBottom")

img1.src = instaIcon
img2.src = facebookIcon
img3.src = twitterIcon

footer.appendChild(img1);
footer.appendChild(img2);
footer.appendChild(img3);

homeLoad()

aboutButton.addEventListener("click", aboutLoad);
homeButton.addEventListener("click", homeLoad);
menuButton.addEventListener("click", menuLoad);



